<template>
  <div>
    <div>
      <!--面包屑-->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>系统管理</el-breadcrumb-item>
        <el-breadcrumb-item>基础信息</el-breadcrumb-item>
        <el-breadcrumb-item>商品基本信息</el-breadcrumb-item>
        <el-breadcrumb-item class="attrbute">
          <span @click="close">类型管理</span>
        </el-breadcrumb-item>
        <el-breadcrumb-item>属性设置</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="组织名称" size="mini">
            <erpCompanyList v-model="params.company_id" :fatherParams='params' :clearable='false' />
          </el-form-item>
          <el-form-item label="属性名称" size="mini">
            <el-input v-model="params.attribute_name" @keyup.enter.native="onSearch" clearable></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
        <div class="add_delet">
          <el-button icon="el-icon-circle-plus-outline" type="primary" class="addBtn" size="mini" @click="add">新增</el-button>
          <el-button plain icon="el-icon-delete" size="mini" @click="attrDelFun">删除</el-button>
        </div>
      </div>
      <div class="table_container">
        <!-- :ajax='{ url: "getTypeList", params: params }' -->
        <xTable ref='xTable' :tableColumn='tableColumn' :xData='attrTableData' />
      </div>

      <!--新增和修改-->
      <div class="attrVisible" v-if="attrVisible">
        <el-dialog :close-on-click-modal="false" title="属性设置" class="dialogForm" :visible.sync="attrVisible" width="500px" @close="attrVisible = false">
          <el-form ref="attrForm" :model="attrForm" label-width="150px" size="mini">
            <el-form-item label="组织名称" size="mini" v-if='attrForm.is_system'>
              <erpCompanyList v-model="attrForm.company_id" :fatherParams='attrForm' :disabled='attrForm.goods_type_id' />
            </el-form-item>
            <el-form-item label="组织名称" size="mini" v-else prop="company_id" :rules="{required: true, message: '请选择公司', trigger: 'change'}">
              <erpCompanyList v-model="attrForm.company_id" :fatherParams='attrForm' :disabled='attrForm.goods_type_id' />
            </el-form-item>
            <el-form-item label="属性名称" prop="attribute_name" :rules="{required: true, message: '请输入属性名称', trigger: 'blur'}" class="el-form-item_shortInput">
              <el-input v-model="attrForm.attribute_name" placeholder="请输入" :disabled='isUpdate'></el-input>
            </el-form-item>
            <el-form-item label="排序" prop="sort" :rules="{ required: true, label:'排序', validator: $publicFution.isInt , trigger: 'blur' }" class="el-form-item_shortInput">
              <el-input v-model="attrForm.sort" type='number' placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="是否必填项" size="medium" :disabled='isUpdate'>
              <el-radio-group v-model="attrForm.is_required">
                <el-radio :label="1">是</el-radio>
                <el-radio :label="0">否</el-radio>
              </el-radio-group>
            </el-form-item>
            <p class="form-line"></p>
            <p style="margin-bottom: 18px;text-align:center">
              <el-button icon="el-icon-circle-plus-outline" :disabled='isUpdate' type="primary" class="addBtn" size="mini" @click="addAttributeFun">添加可选值</el-button>
            </p>
            <el-form-item label="可选值" class="el-form-item_shortInput" :prop="'attriValueData.' + index + '.attribute_value_name'" :rules="{ validator: checkAttr , trigger: 'change' }"  :key="index" v-for="(attri,index) in attrForm.attriValueData">
              <el-input v-model="attri.attribute_value_name"  :disabled="attri.is_quote || isUpdate" class="shortInput" placeholder="0-99"></el-input>
              <el-button plain class="delAttriBute" :disabled="attri.is_quote || isUpdate" size="mini" @click="attrForm.attriValueData.splice(index, 1)">删除</el-button>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="attrVisible = false">取 消</el-button>
            <el-button class="tx" type="primary" @click="submitAttrForm('attrForm')">确 定</el-button>
          </div>
        </el-dialog>
      </div>

    </div>
  </div>
</template>

<script>
import { tableModule } from "./config/attribute";
export default {
  name: "attribute",
  mixins: [tableModule],
  data() {
    return {
      isAdmin: Number(localStorage.getItem("role_id")) === 1 ? true : false, // 是否是管理员
      params: {
        company_id: localStorage.getItem("company_id"),
        company_name: localStorage.getItem("company_name")
      },
      attrVisible: false,
      attrForm: {
        attriValueData: []
      },
      attrTableData: [],
      copyAttrBox: []
    };
  },
  props: ["currentRow"],
  created() {
    this.onSearch();
  },
  computed: {
    isUpdate() {
      // 如果是系统级别属性  只能管理员修改 不是谁都可以修改
      return !(
        (Number(this.attrForm.is_system) == 1 && this.isAdmin) ||
        Number(this.attrForm.is_system) !== 1
      );
    }
  },
  methods: {
    onReset() {
      this.params = {
        company_id: localStorage.getItem("company_id"),
        company_name: localStorage.getItem("company_name")
      };
      this.onSearch();
    },
    onSearch() {
      var params = Object.assign({}, this.params, {
        goods_type_id: this.currentRow.goods_type_id
      });
      this.$axios({
        method: "get",
        url: this.$api.setAttr,
        params: params
      }).then(res => {
        if (res.data.code == 200) {
          this.attrTableData = res.data.data;
        }
      });
    },
    attrDelFun() {
      let addr = new Array();
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择要删除的类型",
          type: "warning"
        });
        return false;
      }
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].attribute_id);
      }
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$axMethod
          .delAttr({
            attribute_id: addr.toString()
          })
          .then(res => {
            if (res.data.code == 200) {
              this.$message.success(res.data.msg);
              this.onSearch();
            }
          });
      });
    },
    add() {
      this.attrForm.attriValueData = [{ attribute_value_name: null }];
      this.attrForm = {
        company_id: localStorage.getItem("company_id"),
        company_name: localStorage.getItem("company_name"),
        attriValueData: []
      };
      this.attrVisible = true;
    },
    addAttributeFun() {
      this.attrForm.attriValueData.push({ attribute_value_name: "" });
    },
    attrUpdateRow(row) {
      //修改属性
      this.attrVisible = true;
      this.attrForm = this.$publicFution.deepCopy(row);
      this.attrForm.is_required = row.is_required == 0 ? 0 : 1;
      this.$set(this.attrForm, 'attriValueData', [])
      for (var i = 0; i < row.value.length; i++) {
        this.attrForm.attriValueData.push({
          is_quote: row.value[i].is_quote,
          attribute_value_name: row.value[i].attribute_value_name,
          attribute_value_id: row.value[i].attribute_value_id
        });
      }
      this.copyAttrBox = this.attrForm.attriValueData.concat([]);
    },
    close() {
      this.$emit("close");
    },
    // 当属性名称为容量是校验可选值
    checkAttr(rule, value, callBack) {
      if (this.attrForm.attribute_name === "容量") {
        var reg = /^[1-9]\d*(\.\d|\d)?\d*(L|mL)$/i;
        if (!reg.test(value)) {
          callBack('容量内容未输入完整，请输入数字加"L"或者“mL”的内容')
        }
      }
      callBack()
    },
    submitAttrForm(attrForm) {
      this.$refs[attrForm].validate(valid => {
        if (valid) {
          var attrForms = {};
          var url = "addAttr";
          // 修改
          var attributeValue = [];
          for (var i = 0; i < this.attrForm.attriValueData.length; i++) {
            attributeValue.push(this.attrForm.attriValueData[i].attribute_value_name);
          }
          attributeValue = attributeValue.join(",");
          if (this.attrForm.attribute_id) {
            url = "editAttr";
            var attriValueData = [],
              delValueId = [];
            for (var i = 0; i < this.attrForm.attriValueData.length; i++) {
              attriValueData.push({
                attribute_value_id: this.attrForm.attriValueData[i].attribute_value_id
                  ? this.attrForm.attriValueData[i].attribute_value_id
                  : 0,
                attribute_value_name: this.attrForm.attriValueData[i]
                  .attribute_value_name
              });
            }
            this.copyAttrBox.forEach(val => {
              var isDel = true;
              this.attrForm.attriValueData.forEach(twoVal => {
                if (twoVal.attribute_value_id === val.attribute_value_id) {
                  isDel = false;
                }
              });
              if (isDel) {
                delValueId.push(val.attribute_value_id);
              }
            });
            attrForms = {
              company_id:
                Number(this.attrForm.is_system) == 1
                  ? localStorage.getItem("company_id")
                  : this.attrForm.company_id,
              company_name:
                Number(this.attrForm.is_system) == 1
                  ? localStorage.getItem("company_name")
                  : this.attrForm.company_name,
              goods_type_id: this.attrForm.goods_type_id,
              attribute_id: this.attrForm.attribute_id,
              attribute_name: this.attrForm.attribute_name,
              attribute_value: attributeValue,
              is_required: this.attrForm.is_required,
              sort: this.attrForm.sort,
              del_id: delValueId.join(","),
              save_content: JSON.stringify(attriValueData)
            };
          } else {
            attrForms = {
              company_id: this.attrForm.company_id,
              company_name: this.attrForm.company_name,
              goods_type_id: this.currentRow.goods_type_id,
              attribute_name: this.attrForm.attribute_name,
              attribute_value: attributeValue,
              is_required: this.attrForm.is_required,
              sort: this.attrForm.sort
            };
          }
          this.$axios.post(this.$api[url], attrForms).then(({ data }) => {
            if (data.code === 200) {
              this.$message.success(data.msg ? data.msg : data.data);
              this.attrVisible = false;
              this.onSearch();
            }
          });
        }
      });
    }
  }
};
</script>

