<template>
  <div class="goodsattribute">
    <div id="leixin" v-show="!isShowAttr">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="类型名称" size="mini">
            <el-input v-model="params.goods_type_name"></el-input>
          </el-form-item>
          <el-form-item label="类型编号" size="mini">
            <el-input v-model="params.goods_type_id"></el-input>
          </el-form-item>
          <el-form-item label="助记码" size="mini">
            <el-input v-model="params.logogram"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="getTypelist" v-if="authority.b_add==1" class="addBtn" size="mini">新增
        </el-button>
        <el-button plain icon="el-icon-delete" @click="delFun" size="mini" v-if="authority.b_del==1">删除
        </el-button>
      </div>
      <div class="table_container">
        <xTable :ajax='{ url: "getTypeList", params: params }' ref='xTable' :tableColumn='tableColumn' :cell-class-name='cellClassName' />
      </div>

      <el-dialog :close-on-click-modal="false" title="商品类型管理" center :visible.sync="dialogFormVisible" width="25%">
        <el-form :model="typeForm" ref="typeForm" v-model="typeForm" class="errorItem errorItemMl121">
          <el-form-item label="上级分类">
            <el-cascader placeholder="选择分类" :clearable="true" :options="levelData" :changeOnSelect="true" v-model="typeForm.level">
            </el-cascader>
          </el-form-item>
          <el-form-item label="类型名称" prop="goods_type_name" :rules="{ required: true, message: '请输入类型名称', trigger: 'blur' }">
            <el-input v-model="typeForm.goods_type_name"></el-input>
          </el-form-item>
          <el-form-item label="备注" size="medium">
            <el-input type="text" v-model="typeForm.remark" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('typeForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('typeForm')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <attribute v-if='isShowAttr' @close='closeAttribute' :currentRow='currentRow' />
  </div>
</template>

<script>
import attribute from "./attribute";
import { tableModule } from "./config/goodType.js";
export default {
  name: "systemInfoSalesGoodsattribute",
  mixins: [tableModule],
  data() {
    return {
      authority: {},
      dialogFormVisible: false,
      typeForm: {},
      levelData: [], //上级分类列表
      isShowAttr: false,
      // 类型管理
      params: {
        // 查询
        goods_type_name: "",
        number: "",
        logogram: ""
      }
    };
  },
  methods: {
    closeAttribute() {
      this.isShowAttr = false;
      this.$refs.xTable._proxy("refreshColumn");
    },
    // 禁止删除行
    cellClassName({ row }) {
      if (row.canNotDelete) {
        return "sign_row";
      }
      return "";
    },
    /** 重置时 如需保存默认参数，则： */
    onReset() {
      this.$set(this, "params", {});

      this.onSearch(1);
    },
    // 新增类型
    getTypelist() {
      this.typeForm = {
        goods_type_name: "",
        remark: ""
      };
      this.dialogFormVisible = true;
      this.getLastTypeList();
    },
    onSearch(number) {
      this.$refs.xTable._getData(number);
    },
    delFun() {
      //删除
      var deledata = {
        deleID: "goods_type_id",
        deleSelect: this.multipleSelection,
        deleApi: "delType",
        list: {
          UrlApi: "getTypeList",
          data: this.TableData
        }
      };
      this.$common.BatchDeletion(deledata, "删除", this, this.onSearch);
    },
    getLastTypeList(callback) {
      //获取上级分类
      var vm = this;
      this.levelData = [];
      this.$axios({
        method: "get",
        url: vm.$api.getTypeListOption
      }).then(function(res) {
        var levelList = res.data.data;
        for (var i = 0; i < levelList.length; i++) {
          vm.levelData.push({
            value: levelList[i].goods_type_id,
            label: levelList[i].goods_type_name,
            children: []
          });
          if (typeof levelList[i].sub != "undefined") {
            for (var j = 0; j < levelList[i].sub.length; j++) {
              vm.levelData[i].children.push({
                value: levelList[i].sub[j].goods_type_id,
                label: levelList[i].sub[j].goods_type_name
              });
            }
          }
        }
        for (var s = 0; s < vm.levelData.length; s++) {
          if (vm.levelData[s].children.length == 0) {
            delete vm.levelData[s].children;
          }
        }
        callback && callback();
      });
    },
    // 属性配置
    toSetAttributes(row) {
      this.currentRow = row;
      this.isShowAttr = true;
    },
    //取消新增类型
    cancelForm(typeForm) {
      this.dialogFormVisible = false;
      this.$refs[typeForm].resetFields();
    },
    //编辑类型
    updateRow(row) {
      var box = [];
      if (row.p_id_1) {
        box.push(row.p_id_1);
      }
      if (row.p_id_2) {
        box.push(row.p_id_2);
      }
      this.typeForm = {
        goods_type_name: row.goods_type_name,
        prand_name: row.prand_name,
        goods_type_id: row.goods_type_id,
        p_id: row.p_id,
        p_name: row.p_name,
        remark: row.remark,
        level: box
      };
      this.getLastTypeList(() => {
        this.dialogFormVisible = true;
      });
    },
    submitForm(typeForm) {
      this.$refs[typeForm].validate(valid => {
        if (valid) {
          var url = "addType";
          var levelNum = 1;
          this.typeForm.p_id = 0;
          if (this.typeForm.level && this.typeForm.level.length) {
            levelNum = this.typeForm.level.length + 1;
            this.typeForm.p_id = this.typeForm.level[
              this.typeForm.level.length - 1
            ];
          }
          var typeForm = {
            goods_type_name: this.typeForm.goods_type_name,
            remark: this.typeForm.remark,
            level: levelNum,
            p_id: this.typeForm.p_id
          };
          if (this.typeForm.goods_type_id) {
            // 编辑
            url = "editType";
            typeForm.goods_type_id = this.typeForm.goods_type_id;
          }
          var funcan = {
            eachData: typeForm,
            UrlApi: url,
            closeDialog: (this.dialogFormVisible = false),
            list: {
              UrlApi: "getTypeList",
              data: this.TableData,
              esData: { page: this.goPage }
            }
          };
          this.$common.addAndupdateFun(funcan, this.onSearch);
        }
      });
    }
  },
  activated() {
    this.onSearch(Window.goPage);
  },
  components: {
    attribute
  }
};
</script>

<style scoped lang="less">
.domainInput .el-input,
.domainInput .el-input--small {
  width: 50%;
}
.cancelBtn {
  background: #eeeeee;
  width: 80px;
}
.ml60 {
  margin-left: 60px;
}
.confimBtn {
  background: #ff7f00;
  color: #fff;
  border-color: #ff7f00;
}
.form-line {
  border-top: 1px dashed #ccc;
  margin: 12px 0 15px 0;
}
.delAttriBute {
  margin: 0;
  padding: 7px 15px;
}
</style>
