export const tableModule = {
  data() {
    return {
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center'
        },
        {
          title: '序号',
          type: "index",
          width: 80,
          align: 'center'
        },

        {
          title: '属性名称',
          field: 'attribute_name',
        },
        {
          title: '属性值',
          field: 'attribute_value',
          width: 300
        },
        {
          title: '是否必填',
          field: 'logogram',
          formatter: ({ row }) => {
            return row.is_required == "1" ? "是" : "否";
          },
          width: 300
        },
        {
          field: 'name',
          title: '操作',
          width: 300,
          slots: {
            default: (scope) => {
              return [
                <el-button size="small" type="text" disabled={scope.row.is_system == 1} onClick={() => this.attrUpdateRow(scope.row)} > 修改 </el-button>
              ]
            }
          }
        },
      ]
    }
  }
}